'use strict';

// MODULES //

import { isPrimitive as isNumber } from '@stdlib/assert-is-number' ;
import { isPrimitive as isPositive } from '@stdlib/assert-is-positive-number' ;
import format from '@stdlib/error-tools-fmtprodmsg' ;
import isnan from '@stdlib/assert-is-nan' ;


// MAIN //

/**
* Validates parameters.
*
* @private
* @param {number} mu - mean
* @param {PositiveNumber} b - scale parameter
* @returns {(Error|null)} error or null
*
* @example
* var err = validate( 1.0, 2.0 );
* if ( err ) {
*     throw err;
* }
*/
function validate( mu, b ) {
	if ( !isNumber( mu ) || isnan( mu ) ) {
		return new TypeError( format( 'invalid argument. First argument must be a number and not NaN. Value: `%s`.', mu ) );
	}
	if ( !isPositive( b ) ) {
		return new TypeError( format( '0PB7T', b ) );
	}
	return null;
}


// EXPORTS //

export default validate;
